.header {

  background: linear-gradient( lighten($black,1%) , $black);
  color: white;
  padding: .7em 0;

  & .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    flex-wrap: wrap;
  }

  .logo img {
    width: 35px;
    height: auto;
  }
}

.icon-menu {
  padding: 0 0.5em;
  cursor: pointer;
}

#main-menu {
  flex: 1 1 100%;
}

.menu {
  list-style: none;
  margin: 1rem 0 0 0;
  padding: 0;


  &__item {
    padding: 1em;
    font-size: .9em;
    letter-spacing: 0px;
    font-weight: 600;
    border-bottom: 1px solid rgba(255,255,255,.5);
    &:last-child {
      border-bottom: 0px;
    }

    a {
      color: white;
      padding: .9em 0;

      &:hover {
        border-bottom: 2px solid $amber;
      }
    }
  }
}

/*---------------------------------------------
EMPIEZAN LAS MEDIAS QUERIES
--------------------------------------------*/
@media (min-width: 768px) {
  .header {
    padding: 0;
  }

  nav#main-menu{
    display: block;
    flex: 0 1 auto;
    margin-left: auto;
    order: 1;
  }

  ul.menu {
    display: flex;
    align-items: center;
    margin: 0;
  }

  .menu__item {
    border-bottom: 0;
    padding: .8em .7em;
  }

  .menu__link {
    font-size: .85rem;
  }

}
@media (min-width: 992px) {
  .menu__link {
    font-size: .9rem;
  }
}
