#Portada {
  background: linear-gradient( rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.7) ), url('../img/multimedia.jpg') top left no-repeat;
  background-size: cover;
  padding: 150px 0;

  //filter: grayscale(100%);

}

.Portada_body {
  padding: 24px;
  color: white;
}

.Portada_copy {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;


  h1 {
    font-weight: 700;
    font-size: 3.5em;
    text-align: center;
    font-family: 'proxima-nova', sans-serif;
  }

  .divider {
    width: 80px;
    border-top: 5px solid #fed136 !important;
    margin: 0 auto;
  }

  p {
    margin-top: 24px;
  }
}

/*** SECCIÓN ****/

.seccion {
  padding: 80px 0;

  &:nth-child(2n + 1) {
    background: #eee;
  }
}

.seccion h2 {
  font-weight: 700;
  font-size: 1.7rem;
  margin-bottom: 36px;
  text-align: center;
}

.seccion p {
  font-size: .95rem;
  line-height: 1.4em;
  text-align: justify;
}

.seccion h3 {
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  color: black;
  font-weight: 700;
  font-size: .9em;
  text-align: center;
}

.seccion {

  .box {
    background: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    margin-bottom: 12px;

    .imagen_contenedor {
      width: 100%;
      max-height: 200px;
      overflow: hidden;
    }

    img {
      margin-bottom: 12px;
      margin-right: 0;
    }

    .info {
      padding: 12px;

      text-align: center;


      p {
        font-family: 'Roboto', sans-serif;
        line-height: 1.2em;
        text-align: center;
      }
    }

  }

}

#Recursos {

  .icono {
    text-align: center;
    background: #0d95e8;
    padding: 7px;
    width: 80px;
    height: 80px;
    line-height: 80px;
    border-radius: 50%;
    margin-bottom: 24px;
    color: #fff;

    .fas, .fab {
      font-size: 2rem;
    }
  }

  p {
    padding: 12px;
    text-align: center;
    font-family: 'Roboto', sans-serif;
  }

  article {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

}

footer {
  background: black;
  margin: 0;

  p {
    color: #ffffff;
    font-family: 'proxima-nova', sans-serif;
    padding: 12px 0;
    margin: 0;
  }
}