.btn-curso-secundario {
  background: lighten($curso-color-secundario,10%);
  color: $black !important;
  border-color: $curso-color-secundario;
  &:hover {
    background: lighten($curso-color-secundario,10%);
    border-color: $curso-color-secundario;
  }
  &:active {
    background: darken($curso-color-secundario,10%);
    border-color: $curso-color-secundario;
  }
}