* {
  box-sizing: border-box;
}

html {
  color: $black;
  font-size: 100%;
  line-height: 1.4;

  @media screen and (min-width: 768px){
    font-size: 110%;
  }
}

body {
  font-family: 'proxima-nova', Helvetica, sans-serif;
  font-weight: 400;
}

a {
  color: $curso-color-secundario;

  &:hover {
    text-decoration: none;
    color: darken($curso-color-secundario, 10%);
  }
}
